$color: #912346;

@media only screen and (min-width: 576px) and (max-width: 1199px) {

    .logo {
        display: none;
    }

    #registration {
        margin-top: 20px;
        margin-bottom: 10px;
    }

    .main-info {
        .row {
        h2 {
            font-size: 40px;
        }
    }
    }
    }


@media only screen and (min-width: 576px) and (max-width: 767px) {
    .main-content__image {
        max-width: 250px;
    }
    .logo {
        display: none;
    }
    .menu__items {
        justify-content: center;
    }
    .menu__item a {
        font-size: 14px;
    }
}

@media only screen and (max-width: 575px) {
    .main-content__image {
        max-width: 226px;
    }
    .logo {
        display: none;
    }
    .menu__items {
        flex-direction: column;
        align-items: center;
        li {
            padding: 3px 0;
        }
    }
    .logo-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
     }
     .menu-icon {
        display: block;
        background: $color;
        border: none;
        font-size: 25px;
        width: 50px;
        height: 50px;
        cursor: pointer;
     }
     #registration {
        margin-bottom: 10px;
    }
}
@media only screen and (max-width: 478px) {
    iframe {
        display: none;
    }
    body {
        p {
            font-size: 16px;
          }
    }
    .main-info {
        .row {
        h2 {
            font-size: 30px;
          }
          p {
            font-size: 18px;
          }
        }
    }
    .main-content__image {
        max-width: 300px;
        margin: 0 auto;
    }
    .logo {
        display: none;
    }
    .logo-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
     }
     .menu__items {
        flex-direction: column;
        align-items: center;
        li {
            padding: 3px 0;
        }
        .menu__item a {
            font-size: 18px;
        }
    }
     .menu-icon {
        display: block;
        background: $color;
        border: none;
        font-size: 25px;
        width: 40px;
        height: 40px;
        cursor: pointer;
     }
     #registration {
         margin-bottom: 10px;
         font-size: 15px;
     }
     .subm {
         font-size: 8px;
     }
     .button {
         padding: 5px 15px;
     }
     iframe {
         height: 150px;
     }
     .scheme {
        .links {
            margin-left: 10px;
        }
        img {
            width: 350px;
          }
        }
        .login-container {
            background: url(/img/IMG_7616.jpg) no-repeat;
            background-size: cover;
            
        }
        .modalDialog > div {
            width: 330px;
        }
        .login-form {
            margin-top: -100px;
            margin-bottom: 0px;

        .container{
            height: 100vh;
            width:370px;
        }
    }
}


/*slider*/
@media only screen and (min-width: 768px) and (max-width: 1199px) {
    .main-content__image {
        max-height: 160px;
        max-width: 220px;
    }
}

